//vuex   npm install --save vuex  .
//store.js  center store
import Vue from 'vue';
import Vuex from 'vuex';


Vue.use(Vuex);

export const store = new Vuex.Store({
	//       .
	state: {
		username: null,
		nickname: null,
		token:null,
		isAdmin: null,
		postcode: null,
    	roadAddress: null,
    	jibunAddress:null,
    	extraAddress: null
	},
	//       .
	getters: {
		username: state => {
			return state.username;
		},
		nickname: state => {
			return state.nickname;
		},
		isAuthenticated: state => {
			return state.token !== null
		},
		isAdmin: state => { 
			return state.isAdmin == 'true'
		},
		token: state => {
			return state.token;
		}
	},
	//state        .  Mutations    . (state)  .
	//mutations   . (setTimeout()  .)      . Actions   mutations    .
	//mutations.js  export .
	mutations: {
		clearAuthData (state) {
	    	state.username = null;
	    	state.nickname = null;
	    	state.token = null;
	    	state.isAdmin = null;
	    },
		authUser(state, userData) {
      // vuex  vuex js   .    .
      //  API     . localStorage .
  			state.token = userData.token
  			state.username = userData.username
  			state.nickname = userData.nickname
  			state.isAdmin = userData.isAdmin
  		},
	},
	//Actions  ./actioins.js   .
	actions: {
		 tryAutoLogin({commit}) {
		  const token = localStorage.getItem("access_token")
	      const username = localStorage.getItem("username")
	      const nickname = localStorage.getItem("nickname")
	      const isAdmin = localStorage.getItem("isAdmin")
	      if (!token) {
	        return
	      }   
	      commit('authUser',{token: token, username: username, nickname: nickname, isAdmin: isAdmin})
	    },
	    logout({commit}) {
	      commit('clearAuthData')
	      localStorage.removeItem("access_token")
	      localStorage.removeItem("username")
	      localStorage.removeItem("nickname")
	      localStorage.removeItem("isAdmin")
	    },
	    postcode({state}) {
	      new daum.Postcode({
	            oncomplete: function(data) {
	                //        .

	                //       .
	                //      ('') ,    .
	                var roadAddr = data.roadAddress; //   
	                var extraRoadAddr = ''; //   

	                //    . ( )
	                //     "//" .
	                if(data.bname !== '' && /[||]$/g.test(data.bname)){
	                    extraRoadAddr += data.bname;
	                }
	                //  ,   .
	                if(data.buildingName !== '' && data.apartment === 'Y'){
	                   extraRoadAddr += (extraRoadAddr !== '' ? ', ' + data.buildingName : data.buildingName);
	                }
	                //    ,     .
	                if(extraRoadAddr !== ''){
	                    extraRoadAddr = ' (' + extraRoadAddr + ')';
	                }

	                //      .
	                document.getElementById('postcode').value = data.zonecode;
	                document.getElementById("roadAddress").value = roadAddr;
	                document.getElementById("jibunAddress").value = data.jibunAddress;
	                state.postcode = data.zonecode;
	                state.roadAddress = roadAddr;
	                state.jibunAddress = data.jibunAddress;
	                
	                //       .
	                if(roadAddr !== ''){
	                    document.getElementById("extraAddress").value = extraRoadAddr;
	                    state.extraAddress = extraRoadAddr;
	                } else {
	                    document.getElementById("extraAddress").value = '';
	                }

	                var guideTextBox = document.getElementById("guide");
	                //  ' '  ,    .
	                if(data.autoRoadAddress) {
	                    var expRoadAddr = data.autoRoadAddress + extraRoadAddr;
	                    guideTextBox.innerHTML = '(   : ' + expRoadAddr + ')';
	                    guideTextBox.style.display = 'block';

	                } else if(data.autoJibunAddress) {
	                    var expJibunAddr = data.autoJibunAddress;
	                    guideTextBox.innerHTML = '(   : ' + expJibunAddr + ')';
	                    guideTextBox.style.display = 'block';
	                } else {
	                    guideTextBox.innerHTML = '';
	                    guideTextBox.style.display = 'none';
	                }
	            }
	        }).open();
	    }
	}
});